<template>
	<div class="sub-container">
		<div class="page-title">
			<h2>굿즈</h2>
		</div>
		<div v-if="goodsList" class="product-list clr">
			<GoodsItem
				v-for="goodsItem in goodsList"
				:key="goodsItem.id"
				:goodsItem="goodsItem"
			/>
		</div>
		<div v-else class="program-list clr">
			상품이 준비중입니다
		</div>
		<Pagination
			v-if="totalPage !== -1 && goodsList"
			:listRowCt="15"
			:pageLinkCt="5"
			:totalCount="totalPage"
			:url="'/goods'"
		/>
		<LoadingSpinner :loading="isLoading" />
	</div>
</template>

<script>
import { fetchGoods, fetchGoodsTotalCount } from '@/api/goods';
import GoodsItem from '@/components/GoodsItem.vue';
import Pagination from '@/components/common/Pagination';
import LoadingSpinner from '@/components/common/LoadingSpinner';

export default {
	components: {
		GoodsItem,
		Pagination,
		LoadingSpinner,
	},
	created() {
		let page = this.$route.params.page - 1 || 0;
		this.fetchGoods(page);
		this.fetchGoodsTotalCount();
	},
	data() {
		return {
			current: 1,
			isLoading: false,
			goodsList: [],
			totalPage: -1,
		};
	},
	methods: {
		async fetchGoods(page) {
			this.isLoading = true;
			try {
				const { data } = await fetchGoods(page);
				this.goodsList = data.goodsList;
			} catch (e) {
				alert(e);
			} finally {
				this.isLoading = false;
			}
		},
		async fetchGoodsTotalCount() {
			this.isLoading = true;
			try {
				const { data } = await fetchGoodsTotalCount();
				this.totalPage = data.totalCount;
			} catch (e) {
				alert(e);
			} finally {
				this.isLoading = false;
			}
		},
	},
	watch: {
		$route(to, from) {
			if (to !== from) {
				this.currentPage = this.$route.params.page;
				this.fetchGoods(this.currentPage - 1);
			}
		},
	},
};
</script>

